
import React from 'react';
import '../App.scss';
import { useState, useEffect } from 'react';
import FilmPresentBlock from '../components/layouts/FilmsContainer';
import CategoriesTabs from '../components/categoriesTab/CategoriesTabs';
import storeHistory from '../helpers/storeResearch';

const Films = props => {

    let [airingTodayTVShows, setAiringTodatTVShows] = useState({
        movies: [],
        currentPage: 1,
        totalPages: 1,
        totalResults: 1
    });
    let [topRatedTVShows, setTopRatedTVShows] = useState({
        movies: [],
        currentPage: 1,
        totalPages: 1,
        totalResults: 1
    });
    let [popTVShows, setPopTVShows] = useState({
        movies: [],
        currentPage: 1,
        totalPages: 1,
        totalResults: 1
    });
    
    let [randomShows, setRandomShows] = useState({
        movies: [],
        currentPage: 1,
        totalPages: 1,
        totalResults: 1
    });

    let [currentCategories, setCurrentCategories] = useState('');
    let [currentPage, setCurrentPage] = useState(1);

    let [searchedATVShowTitle, setSearchedATVShowTitle] = useState('am');

    const setCurrentCat = cat => {
        setCurrentCategories(cat);
    }

    const updatePage = action => {
        switch (action) {
            case 'next':
                setCurrentPage(currentPage + 1);
                break;
            case 'prev': 
                if (currentPage > 1) {
                    setCurrentPage(currentPage - 1);
                }
            default:
                break;
        }
    }

    useEffect(() => {
        // let cookiesTable = document.cookies.split(';');

        // let userString = cookiesTable.filter(cookie => {
        //     return cookie.includes('user')
        // });

        // let userCredential = JSON.stringify(userString.split('=')[0]);

        try {
            fetch(`https://api.themoviedb.org/3/search/tv?api_key=61e3358d94b2180476c2e37dd592815f&language=en-US&page=1&query=${ searchedATVShowTitle }&include_adult=false`)
                .then(data => {
                    return data.json();
                })
                .then(response => {
                    setRandomShows({
                        movies: response.results,
                        currentPage: response.page,
                        totalPages: response.total_pages,
                        totalResults: response.total_results
                    });
                })
                .catch(error => {
                    
                })
        } catch (error) {
            
        };
    }, [searchedATVShowTitle]);


    useEffect(() => {
        try {
            fetch(`https://api.themoviedb.org/3/tv/airing_today?api_key=61e3358d94b2180476c2e37dd592815f&language=en-US&page=${currentPage}`)
                .then(data => {
                    return data.json();
                })
                .then(response => {
                    setAiringTodatTVShows({
                        movies: response.results,
                        currentPage: response.page,
                        totalPages: response.total_pages,
                        totalResults: response.total_results
                    });
                })
                .catch(error => {
                    
                })
        } catch (error) {
            
        };
    }, [airingTodayTVShows]);

    useEffect(() => {
        try {
            fetch(`https://api.themoviedb.org/3/tv/popular?api_key=61e3358d94b2180476c2e37dd592815f&language=en-US&page=${currentPage}`)
            .then(data => {
                return data.json();
            })
            .then(response => {
                setPopTVShows({
                    movies: response.results,
                    currentPage: response.page,
                    totalPages: response.total_pages,
                    totalResults: response.total_results
                });
            })
            .catch(error => {
                
            })
        } catch (error) {
            
        };
    }, [popTVShows,]);

    useEffect(() => {
        try {
            fetch(`https://api.themoviedb.org/3/tv/top_rated?api_key=61e3358d94b2180476c2e37dd592815f&language=en-US&page=${currentPage}`)
            .then(data => {
                return data.json();
            })
            .then(response => {
                setTopRatedTVShows({
                    movies: response.results,
                    currentPage: response.page,
                    totalPages: response.total_pages,
                    totalResults: response.total_results
                });
            })
            .catch(error => {
                
            })
        } catch (error) {
            
        };
    }, [topRatedTVShows]);

    async function handleFilmSearch (event) {
        event.preventDefault();
        let searchedFilm = event.target.elements[0].value;

        setSearchedATVShowTitle(searchedFilm);

        const date = new Date().toString().split(" ").splice(1, 4).join(" ");
        const encoder = new TextEncoder();
        const encodeData = encoder.encode(date);
        const dateHash = await crypto.subtle.digest('SHA-256', encodeData);
        let historyObject = {
            title: searchedFilm,
            date: date,
            id: dateHash,
        }
        storeHistory(historyObject);
    }

    return (
        <div className="home-screen">
            <CategoriesTabs setActiveCat={setCurrentCat}/>
            {(
                context => {
                    if(context === "") {
                        return (
                            <div className="header">
                                <div className="header-left">
                                <span>Catégorie : </span>
                                <select name="" id="">
                                    <option value="sortie">Date de sortie</option>
                                    <option value="langue">Votre langue</option>
                                    <option value="age">Votre age</option>
                                </select>
                                </div>
                                <div className="header-right">
                                    <span>Recherche : </span>
                                    <form onSubmit={ handleFilmSearch }>
                                        <input type="text"/>
                                        <button type="submit"><i className="material-icons">search</i></button>
                                    </form>
                                </div>
                            </div>
                        )
                    }
                }
            )(currentCategories)}
            <div className="films-screen">
                {/* <FilmPresentBlock title="LES FILMS LES PLUS RECHERCHÉS" films={popTVShows} />
                <FilmPresentBlock title="LES FILMS EN PLEIN TOURNAGE" films={airingTodayTVShows} />
                <FilmPresentBlock title="ET PROCHAINEMENT" films={upcommingMovies} /> */}
                {(currentCat => {
                    switch (currentCat) {
                        case '':
                            return <FilmPresentBlock title="JOURNAL · SHOWS" films={randomShows} updateLink={updatePage}/>;
                        case 'popular':
                            return <FilmPresentBlock title="SHOWS POPULAIRES" films={popTVShows} updateLink={updatePage}/>;
                        case 'current':
                            return <FilmPresentBlock title="DIFFUSÉS AUJOURD'HUI" films={airingTodayTVShows} updateLink={updatePage}/>;
                        case 'tocome':
                            return <FilmPresentBlock title="SHOWS MIEUX CÔTÉS" films={topRatedTVShows} updateLink={updatePage}/>;
                        default:
                            break;
                    }
                })(currentCategories)}
            </div>
        </div>
    );
}

export default Films;
