
import classes from './ModalMenu.module.scss';

const ModalMenu = props => {
    
    let displayModalMenu = context => {
        return (
            <ul className={context===true ? classes.contextModal : classes.modalHidden}>
                <li className={classes.modal_list_item} onClick={(event) => { props.handleMenuItemClick('movies', false) }}>Cinemathèque</li>
                <li className={classes.modal_list_item} onClick={(event) => { props.handleMenuItemClick('history', false)}}>History</li>
                <li className={classes.modal_list_item} onClick={(event) => { props.handleMenuItemClick('favorite', false)}}>Favorite</li>
                <li className={classes.modal_list_item} onClick={(event) => { props.handleMenuItemClick('exit', false)}}>Paramètres</li>
            </ul>
        );
    }
    return displayModalMenu(props.context);
}

export default ModalMenu;
