import React from 'react';
import History from '../history/History';
import Favorite from './Favorite';
import Settings from '../settings/Settings';
import Movies from "../moviesRoutes/MoviesRoutes";

const CurrentContext = props => {
    let setCurrentContext = context => {
        switch (context) {
            case 'movies':
                return (
                    <Movies />
                );
            case 'history':
                return (
                    <History />
                );
            case 'favorite':
                return (
                    <Favorite />
                )
            case 'exit':
                return(
                    <Settings updateLog={props.updateLog}/>
                )      
            default:
                break;
        }
    }

    return setCurrentContext(props.context);
}

export default CurrentContext;
