
const storeSearch = (dataObject) => {
    console.log('ererereeeeh');

    let currentDB;

    if(!("indexedDB" in window)) return;

    let dbOpenRequest = indexedDB.open("filmsDB", 2);

    dbOpenRequest.onsuccess = event => {
        currentDB = event.target.result;
        console.log("onsuccess running");

        let dbHistoryTransaction = currentDB.transaction(['searchistory'], "readwrite");

        let store = dbHistoryTransaction.objectStore("searchistory");

        let addRequest = store.add(dataObject);

        addRequest.onerror = event => {
            console.log("Adding history error, " + event.target.error);
        }

        addRequest.onsuccess = event => {
            console.log("history item adding performed with success");
        }

        console.log('orororooooh');
    }

    dbOpenRequest.onerror = event => {
        console.log('onerror running');
    }
}

export default storeSearch;
