
import { useState } from 'react';
import React from 'react';
import {Link} from "react-router-dom";
import classes from './Tabs.module.scss';

const Tabs = props => {
    let [activeTab, setActiveTab] = useState(1);

    let handleFilmTabClick = event => {
        setActiveTab(1);
    };

    let handleFavTabClick = event => {
        setActiveTab(2);
    };

    let handleHistTabClick = event => {
        setActiveTab(3);
    }

    let activeTabs = context => {
        if (context==='movies') {
            return (
                <div className={classes.navigation}>
                    <ul className={classes.navTabs}>
                        <li className={activeTab===1 ? classes.borderBottom : classes.menuTab} onClick={() => { handleFilmTabClick() }}><Link to="/">FILMS <i className="material-icons">movie</i></Link></li>
                        <li className={activeTab===2 ? classes.borderBottom : classes.menuTab} onClick={() => { handleFavTabClick() }}><Link to="/shows">TV SHOWS<i className="material-icons">beenhere</i></Link></li>
                        <li className={activeTab===3 ? classes.borderBottom : classes.menuTab} onClick={() => { handleHistTabClick() }}><Link to="/actors">ACTEURS<i className="material-icons">history_toggle_off</i></Link></li>
                    </ul>
                </div>
            );
        } else {
            return (
                <div></div>
            )
        }
    }

    return activeTabs(props.context);
}    

export default Tabs;
