
import React, { useEffect } from 'react';
import FilmItem from './FilmItem'
import '../../App.scss'
import { useState } from 'react';

const Favoris = props => {
    let [favoriteMovies ,setFavoriteMovies] = useState([]);
    let [modalDetail, setModalDetail] = useState({
        visible: false,
        title: '',
        rate: 0,
        overview: '',
        genres: '',
        popularity: 0,
        adult: 'Non'
    });
    // let [effectTimes, setEffectTimes] = useState(0);

    const updateModal = modalInfo => {
        setModalDetail({
            visible: modalInfo.visible,
            title: modalInfo.title,
            rate: modalInfo.rate,
            overview: modalInfo.overview,
            genres: modalInfo.genres,
            popularity: modalInfo.popularity,
            adult: modalInfo.adult
        })
    }
    useEffect(() => {
        let dbOpenRequest = indexedDB.open("filmsDB", 2);

        dbOpenRequest.onsuccess = event => {
            let currentDB = event.target.result;

            let transaction = currentDB.transaction(["favorite"], "readonly");
            let store = transaction.objectStore("favorite");

            const items = currentDB.transaction("favorite").objectStore("favorite").getAll();

            items.onsuccess = event => {
                setFavoriteMovies(event.target.result);
            }

            // let cursor = store.openCursor();

            // cursor.onsuccess = event => {
            //     let result = event.target.result;

            //     if (result) {
            //         let newFavoriteItems = [...favoriteMovies, result.value];
            //         setFavoriteMovies(newFavoriteItems);
            //         result.continue();
            //     }
            // }

        };

       }, []);
    return (
        <div className="favorite-film-screen">
            <h1 className="favorite-title">VOS DISQUES FAVORIS</h1>
            <div className="films-block">
                {
                    favoriteMovies.map(film => {
                        return <FilmItem filmData={film} showModal = {updateModal} />
                    })
                }
            </div>
            {
                (visible => {
                    if (visible) {
                        return (
                            <div className="modal">
                                <div className="modal-detail">
                                    <div className="modal-header">
                                        <p>Titre: {modalDetail.title}</p>
                                    </div>
                                    <div className="modal-img">
                                        <img src="" alt=""/>
                                    </div>
                                    <div className="modal-body">
                                        <p>Date de sortie: <span>{modalDetail.date}</span></p>
                                        <p>Notes: <span>{modalDetail.rate}</span></p>
                                        <p>Description: <span>{modalDetail.overview}</span></p>
                                        <p>Genres: <span>{modalDetail.genres}</span></p>
                                        <p>Popularité: <span>{modalDetail.popularity}</span></p>
                                    </div>
                                    <a className="btn-close" onClick={() => { updateModal({visible: false})}}>Fermer</a>
                                    <div className="modal-footer">
                                        <p><i className="material-icons">info</i> Pour adultes: {modalDetail.adult}</p>
                                    </div>
                                </div>
                            </div>
                        );
                    }
                })(modalDetail.visible)    
            }
            <h3 className="btn-delete"><i className="material-icons">delete</i> Clear Favorite </h3>
        </div>
    );
};

export default Favoris;
