
import React, { useState } from 'react';
import classes from './CategoriesTabs.module.scss';

const CategoriesTabs = props => {
    let [activeTab, setActiveTab] = useState(0);

    const updateTab = (cat, tab) => {
        props.setActiveCat(cat);
        setActiveTab(tab)
    }
    return (
        <ul className={classes.categoriesMenu}>
            <li className={activeTab===0 ? classes.categoryActive : ''} onClick={() => { updateTab('', 0)}}>JOURNAL</li>
            <li className={activeTab===1 ? classes.categoryActive : ''} onClick={() => { updateTab('popular', 1)}}>POPULAIRE</li>
            <li className={activeTab===2 ? classes.categoryActive : ''} onClick={() => { updateTab('current', 2)}}>EN COURS</li>
            <li className={activeTab===3 ? classes.categoryActive : ''} onClick={() => { updateTab('tocome', 3)}}>A PARAÎTRE</li>
        </ul>
    );
}

export default CategoriesTabs;
