
import React, { useEffect } from 'react';
import { useState } from 'react';

import Footer from '../layouts/Footer';
import Hero from '../hero/Hero';
import CurrentContext from '../layouts/CurrentContext';
import Tabs from '../mainTabs/Tabs';
import ModalMenu from '../menumodal/ModalMenu';
import MovieScroll from '../topMovies/movieScroll';

import { BrowserRouter as Router} from "react-router-dom";
import classes from './PagesContainer.module.scss'; 

const Nav = props => {
    let [contextModal, setContextModal] = useState(false);
    let [context, setContext] =useState('movies');
    let [signIn, setSignIn] = useState(JSON.parse(sessionStorage.getItem("usercredential")));

    let updateLogin = (userCredential) => {
        setSignIn({userCredential});
    };

    useEffect(() => {
        if (!('Notification' in window)) {
            alert('Ce navigateur ne prend pas en charge la notification de bureau')
          }
        
          //  Verify if permission is granted
          else if (Notification.permission === 'granted') {
            // the create a notification
            if((sessionStorage.getItem("usercredential")==null)) {
                const notification = new Notification('Inscription temporaire', {
                    body:"Veuillez bien configurer dans les paramètres de session votre profil pour nous permettre de vous proposer des films qui vous plaisent",
                    icon: "../images/background.jpg"
                });
            }
          }
        
          // Ask the permission to the user
          else if (Notification.permission !== 'denied') {
            Notification.requestPermission().then((permission) => {
              // Make a notification if the user agree
              if (permission === 'granted') {
                const notification = new Notification('Inscription temporaire', {
                    body:"Veuillez bien configurer dans les paramètres de session votre profil pour nous permettre de vous proposer des films qui vous plaisent",
                    icon: "../images/background.jpg"
                });
              }
            })
          }
    }, [])

    useEffect(() => {
        let dbOpenRequest = indexedDB.open("filmsDB", 1);
        
        dbOpenRequest.onupgradeneeded = event => {
            let dataBaseObject = event.target.result;
            console.log('onUpgradeNeeded activated');

            if(!(dataBaseObject.objectStoreNames.contains("favorite"))){
                dataBaseObject.createObjectStore("favorite", {keyPath: 'id'});
            }
        }
    });

    useEffect(() => {
        let dbOpenRequest = indexedDB.open("filmsDB", 2);

        dbOpenRequest.onupgradeneeded = event => {
            let dataBaseObject = event.target.result;
            console.log('onUgradeNeeded activated');

            if(!(dataBaseObject.objectStoreNames.contains("searchistory"))){
                dataBaseObject.createObjectStore("searchistory", {keyPath: 'id'});
            }
        }
    }, [])

    let hideModalMenu = () => {
        setContextModal(false);
    };

    let handleMenuItemClick = (context, modalVisible) => { 
        setContext(context); 
        setContextModal(modalVisible);

        console.log(context);
    }

    let showModalMenu = e => {
        e.stopPropagation(); 
        setContextModal(true);
    };

    return (
        <Router>
            <ModalMenu handleMenuItemClick={ handleMenuItemClick } context= {contextModal}/>
            <div className={classes.nav} onClick={() => { hideModalMenu() }}>
                <div className={classes.appbar}>
                    <div className={classes.navLeft}>
                        <img className={classes.appbarBrandLogo} src="../../img/moviesico.png" alt="moviesico"/>
                        <span className={classes.appbarBrandText}>MoviesUp</span>
                    </div>
                    <div className={classes.navRight}>
                        <a className={classes.appbarPseudo} href="#">{signIn ? signIn.pseudo : 'pseudo'}</a>
                        <a href="#" className={classes.appbarMenu} onClick={(e) => { showModalMenu(e)}}><i className="material-icons">more_vert</i></a>
                    </div>
                </div>
                <Hero context={context}/>
                <MovieScroll />
                <Tabs context={context}/>
            </div>
            <div className={classes.pageContent}>
                <CurrentContext context = {context} updateLog= {updateLogin} />
            </div>
            <MovieScroll />
            <Footer />
        </Router>
    );
}

export default Nav;

