
import React, { useState, useEffect } from 'react';
import HistoryCard from './HistoryCard'

const History = props => {

    let [historyList, setHistoryList] = useState([]);

    useEffect(() => {
        let dbOpenRequest = indexedDB.open("filmsDB", 2);

        dbOpenRequest.onsuccess = event => {
            let currentDB = event.target.result;

            let transaction = currentDB.transaction(["searchistory"], "readonly");
            let store = transaction.objectStore("searchistory");

            const items = currentDB.transaction("searchistory").objectStore("searchistory").getAll();

            items.onsuccess = event => {
                setHistoryList(event.target.result);
            }
        };

       }, [historyList]);

       const handleDeleteAllClick = () => {
            let dbOpenRequest = indexedDB.open("filmsDB", 2);

            dbOpenRequest.onsuccess = event => {
                let currentDB = event.target.result;

                let transaction = currentDB.transaction(["searchistory"], "readwrite");
                let store = transaction.objectStore("searchistory");
                let dbDeleteRequest = store.clear()
                
                dbDeleteRequest.onsuccess = event => {
                    console.log("Clear the search history");
                }

                dbDeleteRequest.onerror = event => {
                    console.log("Oops! History clearing failed");
                }
            };
       }
    return (
        <div className="history-screen">
            <h2 className="page-title">Research History</h2>
            <div className="history-container">
                {
                    historyList.map(history => {
                        return <HistoryCard historyTitle={history.title} historyDate={history.date} />
                    })
                }
            </div>
            <a className="btn-delete" onClick= { handleDeleteAllClick }><i className="material-icons">delete</i> Clear History </a>
        </div>
    );
}

export default History;
