
import React from 'react';
import '../App.scss';
import { useState, useEffect } from 'react';
import FilmPresentBlock from '../components/layouts/FilmsContainer';
import storeHistory from '../helpers/storeResearch';

const Actors = props => {

    let [popActors, setPopActors] = useState({
        movies: [],
        currentPage: 1,
        totalPages: 1,
        totalResults: 1
    });

    let [currentPage, setCurrentPage] = useState(1);

    let [searchedActor, setSearchedActor] = useState('meson');

    const updatePage = action => {
        switch (action) {
            case 'next':
                setCurrentPage(currentPage + 1);
                break;
            case 'prev':
                if (currentPage > 1) {
                    setCurrentPage(currentPage - 1);
                }
                break;
            default:
                break;
        }
    }

    useEffect(() => {
        try {       
            fetch(`https://api.themoviedb.org/3/search/person?api_key=61e3358d94b2180476c2e37dd592815f&language=en-US&query=${ searchedActor }&page=1&include_adult=false`)
                .then(data => {
                    return data.json();
                })
                .then(response => {
                    setPopActors({
                        movies: response.results,
                        currentPage: response.page,
                        totalPages: response.total_pages,
                        totalResults: response.total_results
                    });
                })
                .catch(error => {

                })
        } catch (error) {

        }
    }, [searchedActor]);

    async function handleFilmSearch (event) {
        event.preventDefault();
        let searchedFilm = event.target.elements[0].value;
        setSearchedActor(searchedFilm);
        const date = new Date().toString().split(" ").splice(1, 4).join(" ");
        const encoder = new TextEncoder();
        const encodeData = encoder.encode(date);
        const dateHash = await crypto.subtle.digest('SHA-256', encodeData);
        let historyObject = {
            title: searchedFilm,
            date: date,
            id: dateHash,
        }
        storeHistory(historyObject);
    }

    return (
        <div className="home-screen">
            {/* <CategoriesTabs setActiveCat={setCurrentCat}/> */}
            <div className="header">
                <div className="header-left">
                    <span>Trier par : </span>
                    <select name="" id="">
                        <option value="sortie">Date de sortie</option>
                        <option value="langue">Votre langue</option>
                        <option value="age">Votre age</option>
                    </select>
                </div>
                <div className="header-right">
                    <span>Recherche : </span>
                    <form onSubmit={handleFilmSearch}>
                        <input type="text" />
                        <button type="submit"><i className="material-icons">search</i></button>
                    </form>
                </div>
            </div>
            <div className="films-screen">
                {/* <FilmPresentBlock title="LES FILMS LES PLUS RECHERCHÉS" films={popMovies} />
                <FilmPresentBlock title="LES FILMS EN PLEIN TOURNAGE" films={nowPlayingMovies} />
                <FilmPresentBlock title="ET PROCHAINEMENT" films={upcommingMovies} />
                {(currentCat => {
                    switch (currentCat) {
                        case '':
                            return <FilmPresentBlock title="JOURNAL · SERIES" films={randomMovies} />;
                        case 'popular':
                            return <FilmPresentBlock title="SERIES POPULAIRES" films={popMovies} />;
                        case 'current':
                            return <FilmPresentBlock title="SERIES EN PLEIN TOURNAGE" films={nowPlayingMovies} />;
                        case 'tocome':
                            return <FilmPresentBlock title="SERIES À VENIR" films={upcommingMovies} />;
                        default:
                            break;
                    }
                })(currentCategories)} */}
                <FilmPresentBlock categoryContext='actors' title="ACTEURS POPULAIRES" films={popActors} updateLink={updatePage} />
            </div>
        </div>
    );

}

export default Actors;
