
import { useEffect, useState } from "react";
import classes from './movieScroll.module.scss';

const MovieScroll = props => {

    let [trendingMovies ,setTrendingMovies] = useState({
        movies: [],
        currentPage: 1,
        totalPages: 1,
        totalResults: 1
    });
    
    let [currentPage, setCurrentPage] = useState(1);

    useEffect(() => {
        fetch(`https://api.themoviedb.org/3/trending/all/day?api_key=61e3358d94b2180476c2e37dd592815f`)
            .then(data => {
                return data.json();
            })
            .then(response => {
                setTrendingMovies({
                    movies: response.results,
                    currentPage: response.page,
                    totalPages: response.total_pages,
                    totalResults: response.total_results
                });
            })
    }, [trendingMovies]);

    const handleNextFilms = () => {
        setCurrentPage(currentPage++);
    }
    let mediaGlobaLink = "https://www.themoviedb.org/t/p/w220_and_h330_face/";

    return (
        <div className={ classes.trending}>
            <div className={ classes.trendingHead}>
                <span>Films de la semaine</span>
                <span onClick = { handleNextFilms}>Suivant</span>
            </div>
            <div className={ classes.trendingBody}>
                {trendingMovies.movies.map(movie => {
                    return (
                        <div className={classes.trendingMovieCard}>
                            <img className={ classes.cardBackMedia} src={mediaGlobaLink + movie.backdrop_path} alt="backdrop_path"/>
                            <img className ={ classes.cardPostMedia} src={mediaGlobaLink + movie.poster_path} alt="poster_path"/>
                            <div className={ classes.cardFoot}>
                                {/* <h5 className={classes.footTitle}>title</h5> */}
                                <div className={classes.footInfo}>
                                    {/* <span>{movie.original_title}</span> */}
                                    <span>{movie.original_title}</span>
                                </div>
                            </div>
                        </div>
                    )
                })}
            </div>
        </div>
    );
}

export default MovieScroll;
