
import React from 'react';
import classes from './Hero.module.scss';
import ScndHero from '../layouts/SecondHero';

const Hero = props => {
    return (
        (context => {
            if (context === 'movies') {
                return (
                    <div className={classes.hero}>
                        <h1 className={classes.hero__text}>LE MOMENT EST VENU POUR FINALEMENT TROUVER UN REFUGE POUR LES ANGOISSES ET STRESS DE LA VIE QUOTIDIENNE <br/><span className={classes.hero__textFooter}>Mes films, mon plaisir !</span></h1>
                        <div className={classes.hero__action}>
                            <a href="#">GET STARTED</a>
                            <a href="#">CONTACT US</a>
                        </div>
                    </div>
                );
            } else {
                return <ScndHero classes={classes.secondHero}/>
            }
        })(props.context)
    );
}

export default Hero;
