
import React, { useCallback } from 'react';
import '../App.scss';
import { useState, useEffect } from 'react';
import FilmPresentBlock from '../components/layouts/FilmsContainer';
import CategoriesTabs from '../components/categoriesTab/CategoriesTabs';
import storeHistory from '../helpers/storeResearch';

const Films = props => {

    let [nowPlayingMovies, setNowPlayingMovies] = useState({
        movies: [],
        currentPage: 1,
        totalPages: 1,
        totalResults: 1
    });
    let [upCommingMovies, setUpCommingMovies] = useState({
        movies: [],
        currentPage: 1,
        totalPages: 1,
        totalResults: 1
    });
    let [popularMovies, setPopularMovies] = useState({
        movies: [],
        currentPage: 1,
        totalPages: 1,
        totalResults: 1
    });

    let [randomMovies, setRandomMovies] = useState({
        movies: [],
        currentPage: 1,
        totalPages: 1,
        totalResults: 1
    });

    let [currentCategories, setCurrentCategories] = useState('');

    const setCurrentCat = cat => {
        setCurrentCategories(cat);
    }

    let [currentPage, setCurrentPage] = useState(1);

    let [searchedMovieTitle, setSearchedMovieTitle] = useState('you');

    useEffect(() => {

        try {
            fetch(`https://api.themoviedb.org/3/search/movie?api_key=61e3358d94b2180476c2e37dd592815f&language=${'en'}&query=${searchedMovieTitle}&page=${currentPage}&include_adult=${4>18 ? 'true' : 'false'}&region=${'us'}`)
            .then(data => {
                return data.json();
            })
            .then(response => {
                setRandomMovies({
                    movies: response.results,
                    currentPage: response.page,
                    totalPages: response.total_pages,
                    totalResults: response.total_results
                });
            })
            .catch(error => {
                
            })
        } catch (error) {
            
        }
    }, [searchedMovieTitle]);


    useEffect(() => {
        try {
            fetch(`https://api.themoviedb.org/3/movie/popular?api_key=61e3358d94b2180476c2e37dd592815f&language=en-US&page=${currentPage}`)
            .then(data => {
                return data.json();
            })
            .then(response => {
                setPopularMovies({
                    movies: response.results,
                    currentPage: response.page,
                    totalPages: response.total_pages,
                    totalResults: response.total_results
                });
            })
            .catch(error => {

            })
        } catch (error) {
            
        };
    }, [popularMovies]);

    useEffect(() => {
        try {
            fetch(`https://api.themoviedb.org/3/movie/upcoming?api_key=61e3358d94b2180476c2e37dd592815f&language=en-US&page=${currentPage}`)
            .then(data => {
                return data.json();
            })
            .then(response => {
                setUpCommingMovies({
                    movies: response.results,
                    currentPage: response.page,
                    totalPages: response.total_pages,
                    totalResults: response.total_results
                });
            })
            .catch(error => {
                
            })
        } catch (error) {
            
        }
    }, [upCommingMovies]);

    
    useEffect(() => {
        try {
            fetch(`https://api.themoviedb.org/3/movie/now_playing?api_key=61e3358d94b2180476c2e37dd592815f&language=en-US&page=${currentPage}`)
                .then(data => {
                    return data.json();
                })
                .then(response => {
                    setNowPlayingMovies({
                        movies: response.results,
                        currentPage: response.page,
                        totalPages: response.total_pages,
                        totalResults: response.total_results
                    });
                })
                .catch(error => {
                    
                })
        } catch (error) {
            
        };
    }, [nowPlayingMovies]);

    const updatePage = action => {
        switch (action) {
            case 'next':
                setCurrentPage(currentPage + 1);
                break;
            case 'prev': 
                if (currentPage > 1) {
                    setCurrentPage(currentPage - 1);
                }
            default:
                break;
        }
    }

    // const handleFilmSearch = event => {
    //     let filmTitle = event.target.value;
    //     console.log(filmTitle);
    //     
    // }

    async function handleFilmSearch (event) {
        event.preventDefault();
        let searchedFilm = event.target.elements[0].value;

        setSearchedMovieTitle(searchedFilm);

        const date = new Date().toString().split(" ").splice(1, 4).join(" ");
        const encoder = new TextEncoder();
        const encodeData = encoder.encode(date);
        const dateHash = await crypto.subtle.digest('SHA-256', encodeData);
        let historyObject = {
            title: searchedFilm,
            date: date,
            id: dateHash,
        }
        storeHistory(historyObject);
    }

    return (
        <div className="home-screen">
            <CategoriesTabs setActiveCat={setCurrentCat}/>
            {(
                context => {
                    if(context === "") {
                        return (
                            <div className="header">
                                <div className="header-left">
                                <span>Catégorie : </span>
                                <select name="" id="">
                                    <option value="sortie">Date de sortie</option>
                                    <option value="langue">Votre langue</option>
                                    <option value="age">Votre age</option>
                                </select>
                                </div>
                                <div className="header-right">
                                    <span>Recherche : </span>
                                    <form onSubmit={ handleFilmSearch }>
                                        <input type="text"/>
                                        <button type="submit"><i className="material-icons">search</i></button>
                                    </form>
                                </div>
                            </div>
                        )
                    }
                }
            )(currentCategories)}
                        
            <div className="films-screen">
                {(currentCat => {
                    switch (currentCat) {
                        case '':
                            return <FilmPresentBlock title="JOURNAL · FILMS " films={randomMovies} updateLink={updatePage}/>;
                        case 'popular':
                            return <FilmPresentBlock title="FILMS POPULAIRES" films={popularMovies} updateLink={updatePage}/>;
                        case 'current':
                            return <FilmPresentBlock title="FILMS EN PLEIN TOURNAGE" films={nowPlayingMovies} updateLink={updatePage}/>;
                        case 'tocome':
                            return <FilmPresentBlock title="FILMS À VENIR" films={upCommingMovies} updateLink={updatePage}/>;
                        default:
                            break;
                    }
                })(currentCategories)}

                {/* <FilmPresentBlock title="LES FILMS LES PLUS RECHERCHÉS" films={popularMovies} />
                <FilmPresentBlock title="LES FILMS EN PLEIN TOURNAGE" films={nowPlayingMovies} />
                <FilmPresentBlock title="ET PROCHAINEMENT" films={upcommingMovies} /> */}
            </div>
        </div>
    );
}

export default Films;
