
import React from 'react';
import '../../App.scss';
import profile from '../images/profile.gif';

const FilmItem = props => {
    let mediaGlobaLink = "https://www.themoviedb.org/t/p/w220_and_h330_face/";

    let LazyImg = () => {
        return (
            <img src={mediaGlobaLink + (props.filmData.poster_path ? props.filmData.poster_path : props.filmData.profile_path)} alt=""/>
        );
    }

    const storeFavoriteFilm = dataObject => {
        console.log('ererereeeeh');

        let currentDB;

        if(!("indexedDB" in window)) return;

        let dbOpenRequest = indexedDB.open("filmsDB", 2);

        dbOpenRequest.onsuccess = event => {
            currentDB = event.target.result;
            console.log("onsuccess running");

            let dbFavoriteTransaction = currentDB.transaction(['favorite'], "readwrite");

            let store = dbFavoriteTransaction.objectStore("favorite");

            let addRequest = store.add(dataObject);

            addRequest.onerror = event => {
                console.log("Adding favorite error, " + event.target.error);
            }

            addRequest.onsuccess = event => {
                console.log("Favorite item adding performed with success");
            }

            console.log('orororooooh');
        }

        dbOpenRequest.onerror = event => {
            console.log('onerror running');
        }

    }

    const handleDetatilClick = () => {
        props.showModal({
            visible: true,
            date: props.filmData.release_date,
            title: props.filmData.title || props.filmData.name,
            language: props.filmData.original_language,
            overview: props.filmData.overview,
            department: props.filmData.known_for_department,
            knownFor: props.filmData.known_for,
            genres: 'Action, Fantastic',
            popularity: props.filmData.popularity,
            adult: props.filmData.adult,
        });
    }
    return (
        <div className="films-container__card">
            <h3 className="card-title">{(props.filmData.title ? props.filmData.title : props.filmData.name)}</h3>
            <div className="card-media">
            <React.Suspense fallback={<img src={profile}/>}>
                <LazyImg />
            </React.Suspense>
                {/* <img src={profile} alt=""/> */}
                <p className="card-announce"><span onClick={() => { handleDetatilClick() }}>Cliquer pour plus de détails</span></p>
            </div>
            <div className="card-footer">
                <a onClick={ ()=> { storeFavoriteFilm(props.filmData) }}><i className="material-icons">favorite</i></a>
                <a ><i className="material-icons">play_circle_filled</i></a>
                <a>Détails</a>
            </div>
        </div>
    );
}

export default FilmItem;
