
import React from 'react';
import FilmItem from './FilmItem';
import { useState } from 'react';
import '../../App.scss';

const FilmPresentBlock = props => {
    let [modalDetail, setModalDetail] = useState({
        visible: false,
        title: '',
        language: '',
        overview: '',
        genres: '',
        department: '',
        knownFor: [],
        popularity: 0,
        adult: 'Non'
    });

    const updateModal = modalInfo => {
        setModalDetail({
            date: modalInfo.date,
            visible: modalInfo.visible,
            title: modalInfo.title,
            adult: modalInfo.adult,
            department: modalInfo.department,
            knownFor: modalInfo.knownFor,
            language: modalInfo.language,
            overview: modalInfo.overview,
            genres: modalInfo.genres,
            popularity: modalInfo.popularity,
            adult: modalInfo.adult
        })
    }
    
    const handleNavigation = action => {
        props.updateLink(action)
    }

    return (
        <div className="filmsBlock">
            <h2 className="film-block_title">{props.title}</h2>
            <div className="films-block">
                {
                    props.films.movies.map(film => {
                        return <FilmItem filmData = {film} showModal= {updateModal}/>
                    })
                }
            </div>
            {
                (visible => {
                    if (visible) {
                        return (
                            <div className="modal">
                                <div className="modal-detail">
                                    <div className="modal-header">
                                        <p>{props.categoryContext ? 'Name' : 'Titre'}: {modalDetail.title}</p>
                                    </div>
                                    <div className="modal-img">
                                        <img src="" alt=""/>
                                    </div>
                                    <div className="modal-body">
                                        {
                                            (context => {
                                                switch (context) {
                                                    case 'actors':
                                                        return (
                                                            <>
                                                            <p>Department: <span>{modalDetail.department}</span></p>
                                                            <p>Best sellers: <dl className="def-list">
                                                                {
                                                                    modalDetail.knownFor.map(seller => {
                                                                        return <div>
                                                                            <dt><span>{seller.original_title}</span></dt>
                                                                            <dd>{seller.overview}</dd>
                                                                        </div>
                                                                    })
                                                                }
                                                            </dl></p>
                                                            <p>popularity: <span>{modalDetail.popularity}</span></p>
                                                        </>
                                                        );
                                                
                                                    default:
                                                        return(
                                                            <>
                                                                <p>Date de sortie: <span>{modalDetail.date}</span></p>
                                                                <p>Language: <span>{modalDetail.language}</span></p>
                                                                <p>Description: <span>{modalDetail.overview}</span></p>
                                                                <p>Genres: <span>{modalDetail.genres}</span></p>
                                                                <p>Popularité: <span>{modalDetail.popularity}</span></p>
                                                            </>
                                                        )
                                                }
                                            })(props.categoryContext)
                                        }
                                    </div>
                                    <a className="btn-close" onClick={() => { updateModal({visible: false})}}>Fermer</a>
                                    <div className="modal-footer">
                                        <p><i className="material-icons">info</i> Pour adultes: {modalDetail.adult ? 'Oui' : 'Non'}</p>
                                    </div>
                                </div>
                            </div>
                        );
                    } 
                })(modalDetail.visible)       
            }
            
            <ul className="film-block_links">
                <li  className="links-more" onClick={() => { handleNavigation('prev') }}><i className="material-icons">chevron_left</i></li>
                <li className="links-more" onClick={() => { handleNavigation('next') }}><i className="material-icons">chevron_right</i></li>
            </ul>
        </div>
    );
}

export default FilmPresentBlock;
